import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import {FormsModule} from '@angular/forms';

import { ApartmentService } from '../../../../api/services/apartment.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import { ApartmentBlock_model } from '../../../../api/models/apartment-block-_model';

@Component({
  selector: 'app-add-block-wrapper',
  templateUrl: './add-block-wrapper.component.html',
  styleUrls: ['./add-block-wrapper.component.scss']
})
export class AddBlockWrapperComponent implements OnInit {

  blockArray:any;
  blockCount:number = 1;
  isEditUser:boolean = false;


  constructor(
    private router: Router,
    private route: ActivatedRoute
  ) { }


  addBlock() {
    this.blockCount++;
    this.blockArray =  Array(this.blockCount).fill(0).map((x,i)=>i);
  }

  getBlockArray(event){
    this.blockArray = event;
    this.blockCount = this.blockArray.length;
  }

  isMobileView() {
    return window.innerWidth <= 576;
  }

  ngOnInit() {


    if(this.route.params['value'].id != undefined){
      this.isEditUser = true;
    }

    this.blockArray =  Array(this.blockCount).fill(0).map((x,i)=>i);

    

  }

}
